
* {
  box-sizing: border-box;
}
body {
  background: #f39c12;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 980px;
  width: 100%;
  padding-top: 30px;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
}

.center-on-page {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


.title, .speed {
  text-align: center;
  text-shadow: 3px 3px 3px grey;
}

/* Reset Select */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #2c3e50;
  background-image: none;
}


/* Custom Select */
.select {
  position: relative;
  display: block;
  max-width: 500px;
  width: 100%;
  height: 3em;
  line-height: 3;
  background: #2c3e50;
  overflow: hidden;
  border-radius: 0.25em;
}

select {
  width: 500px;
  height: 100%;
  margin: 0;
  padding: 10px 10px;
  color: #fff;
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 0 1em;
  background: #34495e;
  pointer-events: none;
}

/* Transition */
.select:hover::after {
  color: #f39c12;
}

.select::after {
  transition: 0.25s all ease;
}

.slidecontainer {
  width: 50%;
  margin-bottom: 20px;
}



.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 20px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.8;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  border-radius: 50%;
  height: 20px;
  background: #2c3e50;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #2c3e50;
  cursor: pointer;
}

.text-field {
  width: 100%;
  max-width: 500px;
  margin-bottom: 20px;

}

textarea {
  width: 100%;
  font-size: 20px;
  height: 300px;
}

.button-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

button:first-child {
  margin-right: 20px;
}

.button-control {
  background-color: #2c3e50;
  border: none;
  color: white;
  display: -webkit-flex;
  display: flex;
  width: 120px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background-color: #232e38;
}


